// sass-mq overrides
$mq-breakpoints: (
  mobile: 360px,
  tablet: 768px,
  tablet-landscape: 1024px,
  desktop: 1280px,
  wide: 1600px
) !default;

// default theme
$theme-colors: (
  primary: #06847b,
  primary-dark: #1e3f36,
  primary-light: #6f8f89,
  primary-gray: #4a4a4a,
  primary-light-gray: #e5e5e5,
  secondary-light-gray: #dddddd,
  warning: #fb9300,
  critical: #f54748
) !default;

// width and spacing
$max-width: 1840px !default;

$module-spacing: (
  mobile: 60px,
  desktop: 150px
) !default;

// fonts
$primary-font: 'Open Sans', sans-serif !default;
$secondary-font: 'Barlow Condensed', sans-serif !default;
